<template>
  <div class="login">
    <h2>Login</h2>
    <hr /><br />
    <form @submit.prevent="onSubmit()">
      <!-- <form action="/login" method="POST"> -->
      <input type="email" name="username" v-model.trim="username" placeholder="이메일 입력" minlength="1" required/>
      <input type="password" name="password" v-model.trim="password" placeholder="비밀번호 입력" minlength="4" maxlength="16" autocomplete="new-password" required/>
      <button type="submit">로그인</button>
    </form>
    <!-- <router-link to="/about">About</router-link> | -->
    <!-- <router-link to="/joinForm">가입하기</router-link> -->
    <br />
    <!-- <h3>SNS 로그인</h3> -->
    <a href="/oauth2/authorization/google?redirect_uri=http://localhost:8080/test">
      <img class="google login-Logo" src="data:image/png;base64," />
    </a>
    &nbsp;
    <a href="/oauth2/authorization/github?redirect_uri=http://localhost:8080/test">
      <img class="github login-Logo" src="https://w7.pngwing.com/pngs/490/316/png-transparent-computer-icons-github-github-icon-cat-like-mammal-carnivoran-black.png" />
    </a>
    &nbsp;
    <a href="/oauth2/authorization/naver?redirect_uri=http://localhost:8080/test">
      <img class="naver login-Logo" src="https://t1.daumcdn.net/cfile/tistory/994331335F52047D05" />
    </a>
    &nbsp;
    <a href="/oauth2/authorization/kakao?redirect_uri=http://localhost:8080/test">
      <img class="kakao login-Logo" src="https://www.gotchawashmall.com/design/gotcha82/gotcha/img/kakao.png" />
    </a>
  </div>
</template>
<script>
import $axios from 'axios'
export default {
  name: 'LoginPage',
  props: {
  },
  data: () => ({
    username: '',
    password: ''
  }),
  methods: {
    onSubmit: function () {
      if (this.username == null || this.password == null) {
        alert('필수값 누락')
        return
      }
      // const vm = this
      const logindata = {
        username: this.username,
        password: this.password
      }
      console.log(logindata)
      $axios
        .post('/login', logindata)
        .then(function (response) {
          alert('data', response.data)
          console.log('data : ', response.data)
          console.log('data type : ', typeof response.data)
        })
        .catch(function (error) {
          console.log(error)
          const url = decodeURIComponent(error.request.responseURL)
          const afterStr = url.split('=')
          const message = afterStr[1].replaceAll('+', ' ')
          alert(message)
        })
    }
  }
}
</script>
<style scoped>

.login-Logo{
  width: 50px;
  height: 50px;
  object-fit: cover;
}
</style>
